import { urls } from "../../routes";
import styles from "./Sidebar.module.css";
import { v4 as uuidv4 } from 'uuid';

import logo from "../../images/logo-icon.svg";
import dashboardIcon from "../../images/dashboard-icon.svg";
import settingsIcon from "../../images/settings-icon.svg";
import profileIcon from "../../images/profile-icon.svg";
import aboutUs from "../../images/about-us-icon.svg";
import logout from "../../images/logout-icon.svg";

const links = [
  {
    key: uuidv4(),
    label: "Dashboard",
    to: urls.dashboard,
    icon: dashboardIcon,
  },
  {
    key: uuidv4(),
    label: "Settings",
    to: urls.settings,
    icon: settingsIcon,
  },
  {
    key: uuidv4(),
    label: "Profile",
    to: urls.profile,
    icon: profileIcon,
  },
  {
    key: uuidv4(),
    label: "About us",
    to: urls.aboutUs,
    icon: aboutUs,
  },
  {
    key: uuidv4(),
    label: "Logout",
    to: urls.logout,
    icon: logout,
  },
];

function Sidebar() {
  return (
    <aside>
      <ul className={styles.list}>
        <div className={styles.logoContainer}>
        <img src={logo} className={styles.logo} alt='logo'/>
        </div>
        {links.map((linkData) => (
          <li className={styles.listItem} key={uuidv4()}>
            <img src={linkData.icon} className={styles.listItem_img} alt='Menu icon'/>
            <a href={linkData.to} className={styles.link}>
              {linkData.label}
            </a>
          </li>
        ))}
      </ul>
    </aside>
  );
}

export default Sidebar;
